import React from 'react';


const CommentsList = ({ comments }) => (
    <>
        <h3>Comments</h3>
        {comments.map( (c, k) => (
            <div className="commnet" key={k}>
                <h4>{ c.userName }</h4>
                <p>{c.text}</p>
            </div>
        ))}
    </>
);

export default CommentsList;